<template>
    <view v-show="isShow" :class="is2 ? 'grid-box grid-box-2' : 'grid-box'">
        <view v-show="isShow" class="grid-view">

            <u-grid :col="4">
                <u-grid-item
				@click="$kt.util.navTo('/pages/public/charm-no')"
				>
                    <view class="grid-item-view">
                        <image @load="loadOfImage" :src="$kt.file.byPath('image/icon/theCharts.svg')" mode="widthFix"
                            class="grid-item-img"></image>
                        <view>魅力榜</view>
                    </view>
                </u-grid-item>
                <u-grid-item
				@click="$kt.util.navTo('/pages/shop/body/static')"
				>
                    <view class="grid-item-view">
                        <image @load="loadOfImage" :src="$kt.file.byPath('image/icon/shop.svg')" mode="widthFix"
                            class="grid-item-img"></image>
                        <view>情趣店铺</view>
                    </view>
                </u-grid-item>
                <u-grid-item>
                    <view @click="$kt.util.navTo('/pages/vip/vip')" class="grid-item-view">
                        <image @load="loadOfImage" :src="$kt.file.byPath('image/icon/vip.svg')" mode="widthFix"
                            class="grid-item-img"></image>
                        <view>会员中心</view>
                    </view>
                </u-grid-item>
                <u-grid-item>
                    <view class="grid-item-view">
                        <image @load="loadOfImage" :src="$kt.file.byPath('image/icon/invite.svg')" mode="widthFix"
                            class="grid-item-img"></image>
                        <view>邀请好友</view>
                    </view>
                </u-grid-item>

            </u-grid>
        </view>
    </view>
</template>


<script>
export default {
    data() {
        return {
            imageLoadCount: 0,
            isShow: false,
            is2: false,
        }
    },
    created() {
        // 2秒后强制显示
        setTimeout(() => {
            this.isShow = true;
        }, 2000);
    },
    methods: {
        loadOfImage(e) {

            this.imageLoadCount++;
            if (this.imageLoadCount >= 4) {
                this.isShow = true;
                this.is2 = true;
                setTimeout(() => {
                    this.is2 = false;
                }, 550);
            }
            console.log(this.imageLoadCount, "this.imageLoadCount");
        }
    }
}
</script>

<style lang="scss" scoped>
.grid-box {
    padding: 20rpx;
    box-sizing: border-box;
}

.grid-box-2 {
    animation: scaleGridBox 0.5s 1;

    .grid-item-img-2 {
        animation: scaleImage 0.5s 1;
    }

    .grid-view-2 {
        animation: scaleGridView 0.5s 1;
    }
}

@keyframes scaleGridBox {
    0% {
        padding: 0;
    }

    100% {
        padding: 20rpx;
    }
}

.grid-view {
    position: relative;
    box-shadow: 0 0 10rpx rgba(0, 0, 0, 0.1);
    padding: 30rpx;
    box-sizing: border-box;
    border-radius: 20rpx;
}


@keyframes scaleGridView {
    0% {
        padding: 0;
    }

    100% {
        padding: 30rpx;
    }
}

.grid-item-view {
    text-align: center;
    color: #666;
    font-size: 28rpx;
}

.grid-item-img {
    width: 60rpx;
    height: 60rpx;
}


@keyframes scaleImage {
    0% {
        width: 0rpx;
        height: 0rpx;
    }

    100% {
        width: 60rpx;
        height: 60rpx;
    }
}
</style>